<script>
import sixteenteams from "./components/16teams";
import eightteams from "./components/8teams";
export default {
  data() {
    return {
      matches: [],
      thirdPlace: [],
      brSettings: [],
      currentClassIndex: 0,
      currentBracket: "BracketA",
      isThreeCols: true,
      isSixteen: false,
      isEight: false,
      classTimer: 6000,
      brTimer: 2000,
      club: null,
      tr: null,
    };
  },
  components: {
    sixteenteams,
    eightteams,
  },

  methods: {
    formateMatchtree(matches, isThird) {
      let i = 0;
      for (let match of matches) {
        for (let d of match) {
          let tempHome = "";
          let tempAway = "";
          if (!isThird) {
            tempHome =
              i > 0 ? `Winner of match ${d.tempHomeTeam}` : d.tempHomeTeam;
            tempAway =
              i > 0 ? `Winner of match ${d.tempAwayTeam}` : d.tempAwayTeam;
          } else {
            tempHome =
              i > 0
                ? `Winner of match ${d.tempHomeTeam}`
                : `Loser of match ${d.tempHomeTeam}`;
            tempAway =
              i > 0
                ? `Winner of match ${d.tempAwayTeam}`
                : `Loser of match ${d.tempAwayTeam}`;
          }

          d.roundName = this.getRoundNames(d.round);
          if (d.home) {
            if (this.isAByTeam(d.home.teamName)) {
              d.home.teamName = "BY";
              d.home.player1 = {};
              d.home.player2 = {};
            }
          }
          if (d.away) {
            if (this.isAByTeam(d.away.teamName)) {
              d.away.player1 = {};
              d.away.player2 = {};
              d.away.teamName = "BY";
            }
          }
          if (!d.home) {
            d.isTemp = true;
            d.home = {
              teamName: tempHome,
              player1: {
                firstName: "player1",
                lastName: "",
                profilePic: "https://cdn.forehand.se/uploads/pic.png",
              },
              player2: {
                firstName: "player2",
                lastName: "",
                profilePic: "https://cdn.forehand.se/uploads/pic.png",
              },
            };
          }
          if (!d.away) {
            d.isTemp = true;
            d.away = {
              teamName: tempAway,
              player1: {
                firstName: "player1",
                lastName: "",
                profilePic: "https://cdn.forehand.se/uploads/pic.png",
              },
              player2: {
                firstName: "player2",
                lastName: "",
                profilePic: "https://cdn.forehand.se/uploads/pic.png",
              },
            };
          }
        }
        i++;
      }
      if (isThird) {
        console.log("in third");
        this.thirdPlace = matches;
        console.log("third place is", this.thirdPlace);
      } else {
        this.matches = matches;
      }
    },
    isAByTeam(name) {
      let teams = [
        "By 10",
        "By 9",
        "By 8",
        "By 7",
        "By 6",
        "By 5",
        "By 4",
        "By 3",
        "By 3",
        "By 1",
      ];
      return teams.includes(name);
    },

    getRoundNames(roundNumber) {
      if (roundNumber >= 16) return `ROUND OF ${roundNumber}`;
      if (roundNumber == 8) return "QUARTER FINAL";
      if (roundNumber == 4) return "SEMI FINAL";
      if (roundNumber == 2) return "FINAL";
      if (roundNumber == 0) return "3rd PLACE";
    },
  },
  async created() {
    let trId = this.$route.query.trId;
    let clubId = this.$route.query.clubId;
    if (!trId || !clubId) return alert("Invalid url, please check again");
    const res = await this.callApi(
      "get",
      `getKnockoutFixtureForTvInitial/${trId}/${clubId}`
    );
    if (res.status === 200) {
      this.club = res.data.club;
      this.tr = res.data.tr;
      if (res.data.matches[0].length == 8) {
        this.isSixteen = true;
      } else {
        this.isEight = true;
      }
      this.formateMatchtree(res.data.matches, false)
      this.formateMatchtree(res.data.thirdPlace, true)
      this.brSettings = res.data.brSetting;
      // if (this.brSettings.length > 1) {
      //   setTimeout(() => {
      //     this.getNextClass();
      //   }, this.classTimer);
      // }
      // if (this.brSettings[0].bracketB_knockout > 0) {
      //   setTimeout(() => {
      //     this.getNextBracket();
      //   }, this.brTimer);
      // }
    } else {
      alert("Something went wrong!");
    }
  },
};
</script>



